@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: rgba(0, 0, 0, 0.8784313725);
  margin: 0;
}

p {
  font-size: 1.2rem;
}

.container {
  width: 100%;
  max-width: 140rem;
  margin: 0 auto;
  display: flex;
}

a {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
  color: rgb(135, 150, 22);
}

h4 {
  font-size: 2rem;
  margin: 0;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1025px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}